body {
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #80d2ce;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.title{
  text-decoration: none;
  color:black;
}
.containing{
  height:80vh;
  /* width: 90%; */
  padding:0 10px;
  border: 2px solid rgb(136, 115, 156);
  overflow-y:scroll;
  overflow-x:scroll;
  border-radius: 1.2%;
  box-shadow: 2px 2px;
}
.containing::-webkit-scrollbar{
  display: none;
}
.row{
  height:40px;
  
}
.result{
  width: 80px;
  color:white;
  text-decoration: none;
  background-color: blue;
}
.result:hover{
  color:white;
  text-decoration: none;
  background-color: blue;
}
.bd{
  border-bottom: 1px solid violet;
}
.icons{
  margin-left: auto;
  display: none;
  
}
.icons a{
  text-decoration: none;
  margin:0 8px;
}
.update:hover .icons{
  display: flex;
}
.card{
  position: absolute;
  top:20%;
  left:30%;
  width:50%;
}
